import React, { Children, cloneElement, Fragment } from 'react';
import { bool, func, node, number, oneOfType, string } from 'prop-types';

const AccordionItem = ({ children, expanded, focus, id, index, onClick, onKeyDown }) => (
  <Fragment>
    {
      Children.map(children, child =>  cloneElement(child, { expanded, focus, id, index, onClick, onKeyDown }))
    }
  </Fragment>
);

AccordionItem.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * Expanded
   */
  expanded: bool,

  /**
   * Focus
   */
  focus: bool,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * Index
   */
  index: number,

  /**
   * on Click
   */
  onClick: func,

  /**
   * on KeyDown
   */
  onKeyDown: func
};

AccordionItem.displayName = 'AccordionItem';

export default AccordionItem;
