import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-accordion-subtitle';

const AccordionSubtitle = ({ children, className, ...remainingProps }) => (
  <span
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </span>
);

AccordionSubtitle.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

AccordionSubtitle.displayName = 'AccordionSubtitle';

export default AccordionSubtitle;
