import { useState } from 'react';

export default (defaultExpandedId, onChange, allowMultipleExpanded) => {
  const [focusedId, setFocusedId] = useState();

  const [expandedId, setExpandedId] = useState(
    !allowMultipleExpanded ? defaultExpandedId.slice(0, 1) : defaultExpandedId
  );

  const updateFocusedId = (e, index, count) => {
    const code = e.charCode || e.keyCode;

    if (code === 35) {
      e.preventDefault();
      setFocusedId(count - 1);
    }

    if (code === 36) {
      e.preventDefault();
      setFocusedId(0);
    }

    if (code === 38) {
      e.preventDefault();
      setFocusedId(index - 1 < 0 ? count - 1 : index - 1);
    }

    if (code === 40) {
      e.preventDefault();
      setFocusedId((index + 1) % count);
    }
  };

  const updateExpandedId = (e, index) => {
    let ids = [...expandedId];
    const disclosureIndex = ids.findIndex(i => i === index);

    if (disclosureIndex === -1 && Number.isInteger(index)) {
      ids = allowMultipleExpanded ? [...ids, index] : [index];
    }

    if (disclosureIndex > -1) {
      ids = [...ids.slice(0, disclosureIndex), ...ids.slice(disclosureIndex + 1)];
    }

    setExpandedId(ids);
    if (typeof onChange === 'function') {
      onChange(index);
    }
  };

  return { focusedId, expandedId, updateFocusedId, updateExpandedId };
};
