import React from 'react';
import { bool, node, number, oneOfType, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-accordion-body';
const CSS_STATE = 'vds-state';

const AccordionBody = ({ children, className, expanded, focus, id, index, ...remainingProps }) => ( // eslint-disable-line
  <div
    aria-hidden={expanded ? 'false' : 'true'}
    className={classnames(CSS_PREFIX, {
      [`${CSS_STATE}--hidden`]: Boolean(expanded) === false
    }, className)}
    id={`accordion-body-${id}`}
    {...remainingProps}
  >
    {children}
  </div>
);

AccordionBody.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Expanded
   */
  expanded: bool,

  /**
   * Focus
   */
  focus: bool,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * Index
   */
  index: number
};

AccordionBody.displayName = 'AccordionBody';

export default AccordionBody;
